body {
  background: url("https://images.unsplash.com/photo-1487035242901-d419a42d17af?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=368&q=80")
    no-repeat;
  height: 100vh;
  background-size: 1000px 1000px;
}

.ui .image {
  margin-top: 5px;
}

.App {
  text-align: center;
  margin-top: 50px;
}

.header {
  background-color: #282c34;
  opacity: 0.8;
  /* min-height: 10vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  /* padding: 5px; */
  text-align: center;
  /* margin-top: 5%; */
  margin-left: 5%;
  margin-right: 5%;
  margin-bottom: 5%;
  border-radius: 6%;
  font-family: Apple Chancery, cursive;
}

.description {
  background-color: #282c34;
  min-height: 8vh;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 30px;
  text-align: center;
  margin-left: 5%;
  margin-right: 5%;
  margin-bottom: 5%;
  margin-top: 5%;
  opacity: 0.8;
  border-radius: 6%;
  font-family: Apple Chancery, cursive;
}

.App-link {
  color: #61dafb;
}

input {
  border: 1px solid black;
  background-color: #d0d0d0;
  width: 50%;
  height: 120px;
  border-radius: 4px;
  position: relative;
  transition: 0.3s all;
}

.label {
  color: black;
  font-weight: bold;
  text-align: center;
  padding: 25px;
}

button.ui.button {
  margin-top: 10px;
  color: white;
  background-color: maroon;
  padding: 12px 28px;
}

button.ui.button:hover {
  background-color: #be3446;
  color: black;
}

.short {
  color: maroon;
  background-color: #fbe7e6;
  border: 1px solid maroon;
  margin: 20px 40px 20px 40px;
}

.score {
  font-size: 32px;
  color: whitesmoke;
  background-color: maroon;
  min-height: 8vh;
  align-items: center;
  justify-content: center;
  padding: 30px;
  text-align: center;
  margin-left: 15%;
  margin-right: 15%;
  margin-bottom: 5%;
  margin-top: 5%;
  opacity: 0.8;
  border-radius: 6%;
  font-family: Apple Chancery, cursive;
}
